body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Video {
   z-index: -1;
   position: absolute;
   top: 0;
   bottom: 0;
   width: 100%;
   height: 100%; 
   overflow: hidden;
   -webkit-animation-name: blur;
           animation-name: blur;
   -webkit-animation-duration: 3s;
           animation-duration: 3s;
   -webkit-animation-delay: 3s;
           animation-delay: 3s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

video#rms {
   min-width: 100%;
   min-height: 100%;
   width: auto;
   height: auto;
   position: absolute;
   top: 50%;
   left: 50%;
   -webkit-transform: translate(-50%,-50%);
           transform: translate(-50%,-50%);
   z-index: -10;
}

@-webkit-keyframes blur {
   to {
      -webkit-filter: blur(20px);
              filter: blur(20px);
   }
}

@keyframes blur {
   to {
      -webkit-filter: blur(20px);
              filter: blur(20px);
   }
}
.Menu {
   position: relative;
   flex: 0 1 auto;
   min-width: 60px;
   padding-top: 160px;
   margin-left: 40px;
   -webkit-transition: 1.5s;
   transition: 1.5s;
}

.Menu_opened {
   width: 400px;
}

.Menu_closed {
   width: 60px;
}

.MenuItems {
   position: fixed;
   -webkit-transition: 0.3s;
   transition: 0.3s;
   -webkit-transition-duration: 1s;
           transition-duration: 1s;
}

.MenuItems_opened {
   -webkit-transition-delay: 1s;
           transition-delay: 1s;
   opacity: 1;
}

.MenuItems_closed {
   -webkit-transition-duration: .2s;
           transition-duration: .2s;
   opacity: 0;
}

.MenuItems ul {
   margin: 0;
   padding: 0;
   list-style: none;
}

.MenuItems li {
   display: flex;
   align-items: center;
   cursor: pointer;
   font-weight: bold;
   font-size: 72px;
   height: 100px;
}

.MenuItems li::before {
   display: inline-block;
   content: '';
   width: 40px;
   height: 10px;
   background-color: white;
   margin-right: 30px;
}

.MenuButton {
   position: fixed;
   top: 65px;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 3s;
           animation-duration: 3s;
   -webkit-animation-delay: 4s;
           animation-delay: 4s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
   cursor: pointer;
}

.MenuInfo {
   bottom: 20px;
}

.MenuInfo span {
   display: block;
   margin-bottom: 7px;
}

@-webkit-keyframes slidein {
   to {
      top: 40px;
   }
}

@keyframes slidein {
   to {
      top: 40px;
   }
}

@-webkit-keyframes fadein {
   to {
      opacity: 1;
   }
}

@keyframes fadein {
   to {
      opacity: 1;
   }
}

.Content {
   height: 0;
   flex: 1 1 auto;
   padding-top: 160px;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 2s;
           animation-duration: 2s;
   -webkit-animation-delay: 5s;
           animation-delay: 5s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

.Text {
   max-width: 500px;
}

@-webkit-keyframes fadein {
   to {
      opacity: 1;
   }
}

@keyframes fadein {
   to {
      opacity: 1;
   }
}

@font-face {
   font-family: 'Inter UI';
   src: url(/static/media/Inter-Regular.6e1bdd36.ttf) format('ttf');
   src: url(/static/media/Inter-Regular.5dabd32d.woff) format('woff');
}

.App {
   display: flex;
   font-family: Inter UI;
   font-style: normal;
   font-weight: normal;
   font-size: 18px;
   line-height: 135%;
   color: #FFFFFF;
}

.Logo {
   position: absolute;
   -webkit-animation-duration: 3s;
           animation-duration: 3s;
   -webkit-animation-name: slidein;
           animation-name: slidein;
   -webkit-animation-delay: 3s;
           animation-delay: 3s;
   top: 50%;
   left: 8%;
   width: 180px;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

